<template>
    <div id="app">
        <div class="container">
            <h1 class="title">모두의 주차장 시스템</h1>

            <!-- 주차 공간 상태 표시 -->
            <div class="parking-status">
                <h2 class="subtitle">주차 공간 상태</h2>
                <!-- 주차 슬롯을 반복하여 렌더링하는 부분 -->
                <div v-for="(slot, index) in parkingSlots" :key="index" class="parking-slot">
                    <!-- 각 슬롯에 대해 'slot'이 true인 경우 'filled' 클래스를,
             false인 경우 'empty' 클래스를 적용 -->
                    <!-- 'slot' < - 기본 클래스 스타일 
  , slot -->
                    <div :class="['slot', slot ? 'filled' : 'empty']">
                        <!-- 슬롯 번호와 상태를 표시하는 텍스트 -->
                        <p class="slot-info">슬롯 {{ index + 1 }}:
                            <!-- slot이 true이면 "차량 있음", 
                  false이면 "빈 공간"으로 상태를 표시
                  v-for="(slot, index) in parkingSlots"에서 index는
                   parkingSlots 배열의 각 요소의 위치(인덱스)를 나타냅니다.
                    예를 들어, parkingSlots 배열이 
                    [false, true, false, false, true]일 경우, 
                     index 값은 다음과 같습니다:
                      첫 번째 슬롯 (index = 0) 
                      두 번째 슬롯 (index = 1) 
                      세 번째 슬롯 (index = 2) 
                      네 번째 슬롯 (index = 3) 
                      다섯 번째 슬롯 (index = 4) -->
                            {{ slot ? "차량 있음" : "빈 공간" }}</p>


                        <button v-if="!slot" @click="parkCar(index)" class="action-btn park-btn">
                            차량 입차
                        </button>
                        <!-- 
              . v-if="!slot"
  v-if는 조건부 렌더링을 위해 사용되는 Vue.js 디렉티브입니다.
   해당 요소가 조건에 맞을 때만 렌더링되도록 합니다.
  
  slot: 주차 공간을 나타내는 변수입니다. 
  이 변수는 true 또는 false 값을 가질 수 있습니다.
  
  true: 해당 주차 공간에 차량이 주차되어 있는 상태입니다.
  false: 해당 주차 공간이 빈 공간인 상태입니다.
  !slot: slot이 false일 때 논리 부정(!) 연산자가 적용되어 true가 됩니다.
  
  즉, !slot은 **"빈 공간"**일 때만 참(true)이 됩니다.
  예를 들어, slot이 false일 때 !slot은 true가 되어 이 요소가 렌더링됩니다.
  따라서 v-if="!slot"는 슬롯이 빈 공간일 때만 해당 요소(버튼)를 화면에 표시하도록 합니다.
   즉, 주차 공간에 차량이 없을 때(슬롯이 비어 있을 때)만 입차 버튼이 보이게 됩니다.
  
  2. @click="parkCar(index)"
  @click은 Vue.js에서 클릭 이벤트를 처리하는 방법입니다. 
  Vue.js에서 @는 v-on:의 단축 표현입니다. v-on:click은 
  클릭 이벤트가 발생했을 때 실행할 메서드를 지정하는데 사용됩니다.
  
  parkCar(index): 클릭 시 호출되는 메서드입니다.
   이 메서드는 index 값을 인수로 받습니다.
  index는 v-for 반복문에서 각 주차 슬롯의 인덱스를 나타냅니다. 
  각 슬롯의 인덱스를 parkCar 메서드에 전달하여 
  해당 슬롯에 차량을 입차할 수 있게 합니다. 
  예를 들어, 
  첫 번째 슬롯이 클릭되면 index는 0이 되고,
   두 번째 슬롯은 index가 1이 됩니다.
  따라서 이 부분은 버튼 클릭 시 해당 슬롯에 차량을 입차시키는 동작을 담당합니다. 
  parkCar(index) 메서드는 index 값을 사용하여 어떤 슬롯에 차량을 입차할지 결정합니다.
  
  종합 설명:
  v-if="!slot": 주차 공간이 빈 공간일 때만 버튼을 표시합니다. 
  즉, slot이 false일 때, 
  즉 해당 슬롯에 차량이 주차되지 않았을 때만 입차 버튼을 화면에 보여줍니다.
  @click="parkCar(index)": 
  사용자가 버튼을 클릭하면 parkCar(index) 메서드가 호출되며,
   해당 슬롯에 차량을 입차시키는 동작을 실행합니다. 
   이때 index는 클릭된 슬롯의 위치를 나타내어, 어떤 슬롯에 차량을 주차할지 결정합니다.
   -->
                        <button v-if="slot" @click="removeCar(index)" class="action-btn remove-btn">
                            차량 출차
                        </button>
                    </div>
                </div>
            </div>
            <!-- 주차된 차량 수 표시 -->
            <div class="status-info">
                <h3>현재 주차된 차량 수: {{ parkedCarsCount }} / {{ totalSlots }}</h3>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        // 컴포넌트 이름 정의 
        name: "App",
        // Vue 컴포넌트의 이름을 "App"으로 설정합니다. 
        //   주로 다른 컴포넌트에서 이 이름을 참조하여 사용할 수 있습니다.

        // 데이터 초기화
        data() {
            return {
                totalSlots: 5, // 주차 공간의 총 개수 (5개의 주차 공간을 설정합니다)

                // parkingSlots는 배열로, 각 주차 공간에 차량이 있는지 여부를 관리합니다.
                // false는 빈 공간, true는 차량이 주차된 공간을 의미합니다.
                parkingSlots: Array(5).fill(false), // 주차 공간을 5개로 초기화하며, 
                //   모두 빈 공간으로 설정합니다.
            };
        },

        // 계산된 속성 (computed property) 컴퓨티드 
        /*
    computed property (계산된 속성)의 역할은 동적으로 변하는 값을 계산하여 반환하는 것입니다. Vue.js와 같은 프레임워크에서 computed property는 주로 데이터의 변화를 자동으로 감지하고, 효율적으로 계산된 값을 반환하는 데 사용됩니다.
  
  1. 자동으로 계산된 값 반환
  computed property는 반드시 필요한 값만을 계산하여 반환합니다.
   예를 들어, 데이터가 변경되었을 때만 계산을 수행하고, 
   이전 값이 변경되지 않으면 재계산을 하지 않아 성능상 유리합니다.
  이는 method와의 차이점으로, method는 호출될 때마다 계산이 일어나지만 
  computed는 의존하는 데이터가 변경될 때만 계산이 실행됩니다.
  2. 데이터 의존성 추적
  computed property는 내부적으로 어떤 데이터에 의존하는지 추적합니다.
   만약 의존하는 데이터가 바뀌면 computed property가 자동으로 다시 계산됩니다.
  예를 들어, computed property에서 
  사용된 값이 변경되면 그에 따라 결과값이 자동으로 갱신됩니다.
  3. 가독성과 유지보수성 향상
  computed property를 사용하면 복잡한 계산 로직을 템플릿이나 
  메서드에서 분리하여 코드의 가독성을 높이고, 유지보수를 쉽게 합니다.
  계산된 값이 여러 곳에서 사용된다면, computed 속성을 이용하여 중복된 계산을 피할 수 있습니다.*/
        computed: {
            // 주차된 차량 수를 계산하는 계산된 속성
            parkedCarsCount() {
                // filter 메서드를 사용하여 parkingSlots 배열에서 
                //   'true'인 값(차량이 주차된 슬롯)만 필터링합니다.
                // length를 사용하여 차량이 주차된 공간의 개수를 반환합니다.
                return this.parkingSlots.filter(slot => slot).length;
                // 주차된 차량의 수를 반환합니다.
            }
        },

        // 메서드 정의
        methods: {
            // 차량 입차 메서드
            // 인덱스를 전달받아 해당 슬롯에 차량을 주차하는 동작을 수행합니다.
            parkCar(index) {
                // 해당 인덱스의 parkingSlots 값을 확인하여 빈 공간인지 확인합니다.
                if (this.parkingSlots[index] === false) {
                    // 빈 공간이라면, 차량을 입차합니다.  
                    this.parkingSlots[index] = true;
                    // 해당 슬롯을 'true'로 변경하여 차량이 주차된 상태로 설정합니다. 
                } else {
                    // 이미 차량이 주차된 공간이라면, 경고 메시지를 표시합니다.
                    alert("이 공간에는 이미 차량이 주차되어 있습니다.");
                }
            },

            // 차량 출차 메서드  
            // 인덱스를 전달받아 해당 슬롯에서 차량을 출차하는 동작을 수행합니다.
            removeCar(index) {
                // 해당 인덱스의 parkingSlots 값을 확인하여 차량이 주차되어 있는지 확인합니다.
                if (this.parkingSlots[index] === true) {
                    // 차량이 주차되어 있다면, 해당 차량을 출차합니다.
                    this.parkingSlots[index] = false;
                    // 해당 슬롯을 'false'로 변경하여 차량을 출차된 상태로 설정합니다.
                } else {
                    // 빈 공간이라면, 경고 메시지를 표시합니다.
                    alert("이 공간은 이미 비어 있습니다.");
                }
            },
        },
    };
</script>

<style>
    * {
        margin: 0;
        padding: 0;
    }

    #app {
        background-color: #edeaea;
        font-family: 'Nanum Gothic';
        color: #333;
        padding: 20px;
        min-height: 100vh;
    }

    #app .container {
        max-width: 800px;
        margin: 0 auto;
        text-align: center;
        background-color: #fff;
        border-radius: 12px;
        padding: 30px;
        box-sizing: border-box;
        box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
    }

    #app .container .title {
        font-size: 3rem;
        color: #333;
        margin-bottom: 30px;
        font-weight: bold;
    }

    #app .container .subtitle {
        font-size: 1.8rem;
        color: #333;
        margin-bottom: 30px;
        font-weight: bold;
    }

    /* 주차공간상태 스타일 */
    #app .container .parking-status {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #app .container .parking-status .parking-slot {
        display: flex;
        flex-direction: column;
        margin: 20px 0;
        width: 100%;
    }

    #app .container .parking-status .parking-slot .slot {
        background-color: #363636;
        padding: 20px;
        border-radius: 8px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
        transition: 0.3s;
    }

    #app .container .parking-status .parking-slot .slot:hover {
        transform: scale(1.05);
        box-shadow: 0 10px 20px rgba(0,0,0,0.15);
    }

    #app .container .parking-status .parking-slot .slot .slot-info {
        font-size: 1.2rem;
        color: #fff;
        font-weight: bold;
    }

    /* <div :class="['slot', slot ? 'filled' : 'empty']" */
    .filled {
        background-color: #f44336;
        color: #fff;
    }

    .empty {
        background-color: #4caf50;
        color: #fff;
    }

    button { 
        padding: 15px 25px;
        border: none;
        border-radius: 10px;
        cursor: pointer;
        font-size: 1.2rem;
        font-weight: bold;
        transition: 0.3s;
        box-shadow: 0 6px 10px rgba(0,0,0,0.1);
    }

    /* 입차 버튼 */
    .park-btn {
        background-color: #4caf50;
        color: #fff;
    }

    .park-btn:hover {
        background-color: #277e3c;
        transform: scale(1.1);
        box-shadow: 0 8px 15px rgba(0,0,0,0.2);
    }

    /* 출차 버튼 */
    .remove-btn {
        background-color: #f44336;
        color: #fff;
    }
    .remove-btn:hover {
        background-color: #d32f2f;
        transform: scale(1.1);
        box-shadow: 0 8px 15px rgba(0,0,0,0.2);
    }

    /* 상태정보 */
    .status-info {
        margin-top: 40px;
        font-size: 1.5em;
        font-weight: bold;
        color: #2c3e50;
    }
</style>